- Font sizes (px):
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98

- Font weights:
Default: 400
Medium: 500
Semi-bold: 600
BOLD: 700

- Line heights:
Default: 1
Small: 1.05
Medium: 1.2
Paragraph default: 1.6
Large 1.8

- Letter Spacing:
-0.5
0.75px

---02 COLORS
- Primary: 

- Tints:

- Shades:

- Accents:

- Greys:


---03 IMAGES
---04 ICONS
---05 SHADOWS

---06 BORDER-RADIUS
---07 WHITESPACE
- SPACING SYSTEM (px):
2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 /128

*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  /* 10px /16px = 0.625 = 62.5%*/
  /*Percentage of user's browser font-settings*/
  font-size: 62.5%;
  overflow-x: hidden;
  /* scroll-behavior: smooth; */
}

body{
  font-family: "Rubik", sans-serif;
  line-height: 1;
  font-weight: 400;
  color: #555;
  overflow: hidden;
 
}

.heading-primary, .heading-secondary, .heading-tertiary {
  font-weight: 700;
  color: #333;
  letter-spacing: -.5px;
}

.heading-primary{
  color: #fff;
  font-size: 8.6rem;
  line-height: 1.05;
  margin-bottom: 3.2rem;
}

.heading-secondary{
  font-size: 4.8rem;
  line-height: 1.05;
  margin-bottom: 3.2rem;
}
/******************************/
/*GENERAL REUSABLE COMPONENTS*/
/*****************************/

.grid{
  display: grid;
  row-gap:9.6rem;
  column-gap: 6.4rem;
}

.grid:not(:last-child){
  margin-bottom: 9.6rem;
}

.grid--2-cols{
  grid-template-columns: repeat(2, 1fr);
}

.grid--3-cols{
  grid-template-columns: repeat(3, 1fr);
}

.grid--4-cols{
  grid-template-columns: repeat(4, 1fr);
}

.grid--5-cols{
  grid-template-columns: repeat(5, 1fr);
}

.grid--center-v{
  align-items: center;
}




.list{
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}

.list-item{
  font-size: 1.8rem;
  display: flex;
  align-items: center;
  gap: 1.6rem;
  line-height: 1.2;
}

.list-icon{
  width: 30px;
  height: 30px;
  color: #e67e22
}

*:focus{
  outline: none;
  box-shadow: 0 0 0 0.8rem rgba(230, 126, 34, 0.5);
}

/* HELPER/SETTINGS CLASSES */

.margin-right-sm{
  margin-right: 1.6rem !important;
}

.margin-bottom-md{
  margin-bottom: 4.8rem !important;
}

.center-text{
  text-align: center;
}